.messageBox {
    display: flex;
    align-items: center;
    width: 100%;
    margin-right: 1rem;
}

.messageBox textarea {
    width: 100%;
    margin-right: .75px;
}

.messages {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.message {
    display: flex;
    margin-bottom: .35rem;
}

.message span {
    position: relative;
    padding: 10px;
    line-height: 1.2;
    margin-left: 1.25rem;
    background-color: rgb(224, 208, 255);
    z-index: 1;
    max-width: 20rem;
    word-wrap: break-word;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.message span::after {
    content: '';
    position: absolute;
    left: -16px;
    top: 0;
    width: 0;
    height: 0;
    border-top: 19px solid transparent;
    border-bottom: 19px solid transparent;
    border-right: 16px solid rgb(224, 208, 255);
    clear: both;
}

.message img {
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
}

.messageWrapper {
    padding: 1.75rem;
    height: calc(80vh - 51px);
    overflow-y: scroll;
    background-color: rgba(63, 41, 104, 0.644);
}

.messageWrapperForm {
    display: flex;
    justify-content: center;
    align-self: center;
    height: calc(20vh - 51px);
    width: 100%;
}

.messageWrapperForm form {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 1.25rem;
}
